import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, AlertController, ViewController, ToastController, PopoverController } from 'ionic-angular';
import { Quote } from '../../data/quotes.interface';
import { QuotesService } from '../../services/quotes';
import { QuotePage } from '../quote/quote';
import { QuotesPage } from '../quotes/quotes';

/**
 * Generated class for the PopoverPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-popover',
  template: `
  <ion-list>
    <ion-list-header>Ionic</ion-list-header>
    <button ion-item (click)="addQuote()">Add Quote</button>
    <button ion-item (click)="close()">Close</button>
  </ion-list>
  `,
})
export class PopoverPage {

  quotes : Quote[];
  totalQuotes = 13;
  constructor(public quoteServices : QuotesService, public navCtrl: NavController, public navParams: NavParams, private modalCtrl: ModalController, private alertCtrl: AlertController, private viewCtrl: ViewController, public toastCtr: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PopoverPage');
  }

  addQuote(){
    this.presentPrompt();
  }

  presentPrompt() {
    let alert = this.alertCtrl.create({
      title: 'Add New Quotes',
      inputs: [
        {
          name: 'author',
          placeholder: 'name'
        },
        {
          name: 'quote',
          placeholder: 'quote'
        }
      ],
      buttons: [
        {
          text: 'OK',
          handler: data => {
            this.quoteServices.addQuoteToFavorites({id:String(this.totalQuotes++), person:data.author, text:data.quote});
            let toast = this.toastCtr.create({
              message : "New Quote is Added!",
              duration : 3000,
              position : "bottom"
            })
            toast.present();
          }
        },
        {
          text: 'CANCEL',
          role: 'cancel',
          handler: data => {
          }
        }
      ]
    });
    alert.present();
  }

  close(){
    this.viewCtrl.dismiss();
  }

}



