import { Component, ViewChild } from '@angular/core';
import { Platform, NavController, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { SettingPage } from '../pages/setting/setting';
import { SignUpPage } from '../pages/sign-up/sign-up';

import firebase from "firebase";
import { LoginPage } from '../pages/login/login';
import { AuthService } from '../services/AuthService';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = TabsPage;
  tabsPage = TabsPage;
  settingPage = SettingPage;
  signUpPage = SignUpPage;
  loginPage = LoginPage;
  signUp = SignUpPage;

  @ViewChild('sideMenuContent') nav: NavController;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private menuCtrl: MenuController, public authServ: AuthService) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

    firebase.initializeApp({
      apiKey: "AIzaSyCpJACoCJ-KwphG7j3CTEH13IbLrvReHFA",
      authDomain: "fairbes-28e0a.firebaseapp.com",
      databaseURL: "https://fairbes-28e0a.firebaseio.com",
      projectId: "fairbes-28e0a",
      storageBucket: "fairbes-28e0a.appspot.com",
      messagingSenderId: "664084706217"
    });

    const unsubscribe = firebase.auth().onAuthStateChanged(user => {
      if(!user){
        this.rootPage = LoginPage;
      }else{
        this.rootPage = TabsPage;
      }
    })
  }

  onLoad(page: any){
    this.nav.setRoot(page);
    this.menuCtrl.close();
  }
  signOut(){
    this.authServ.logout();
  }
  cekUser(){
    return this.authServ.getCurrentUser();
    //console.log(this.authServ.getCurrentUser);
  }
}




