import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ViewController } from 'ionic-angular';
import { QuotesService } from '../../services/quotes';
import { Quote } from '../../data/quotes.interface';

/**
 * Generated class for the QuotesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-quotes',
  templateUrl: 'quotes.html',
})
export class QuotesPage {

  quotes : Quote[]; 

  constructor(public quoteServices : QuotesService, public navCtrl: NavController, public navParams: NavParams, private alertCtrl: AlertController, private viewCtrl: ViewController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QuotesPage');
  }

  ngOnInit(){
    this.quotes= this.navParams.get('Quote');
  }

  onShowAlert(quote : Quote){
    const alert = this.alertCtrl.create({
      title: 'Remove Quote',
      message: 'Are you sure you want to remove the quote from favorites?',
      buttons: [
        {
          text: 'YES',
          handler: () => {
            this.quoteServices.removeQuoteFromFavorites(quote);
            this.viewCtrl.dismiss();
          }
        },
        {
          text: 'NO'
        }
      ]
    });
    alert.present();
  }

  dismissModal(){
    this.viewCtrl.dismiss();
  }

}



