import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { NgForm } from '@angular/forms';
import { AuthService } from '../../services/AuthService';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  constructor(public loadingCtrl: LoadingController,public navCtrl: NavController, public navParams: NavParams, public authServ : AuthService) {
  }

  ionViewDidLoad() {
    console.log(this.authServ.getCurrentUser());
    console.log('ionViewDidLoad LoginPage');
  }

  submitForm(form : NgForm){
    console.log(form.value.email);
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: 'Loading Please Wait...'
    });

    loading.present();
    this.authServ.signin(form.value.email, form.value.pass).then(
      data => {
        loading.dismiss();
      }
    )
    .catch(err => {
      console.log('Failed to Login');
    })
  
  }

}



