import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertCmp, AlertController } from 'ionic-angular';
import { Quote } from '../../data/quotes.interface';
import { QuotesService} from '../../services/quotes';

/**
 * Generated class for the QuotePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-quote',
  templateUrl: 'quote.html',
})
export class QuotePage implements OnInit {
  quotes : Quote[];

  constructor(public quoteServices : QuotesService, public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams) {

  }

  

  ngOnInit(){
    this.quotes = this.navParams.get('list');
    // console.log(this.navParams.get('list'));
    
    // for(let i =0;i<this.quoteServices.getAllFavoriteQuotes.length; i++){
        
    // }
    // for(let data of this.quoteServices.getAllFavoriteQuotes[0])
    // console.log(document.getElementById("id"));
    

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad QuotePage');
  }

  onShowAlert($list, $flag){
    if($flag == 'F'){
      const alert = this.alertCtrl.create({
        title : 'Add Quote',
        message : 'Are Your Sure You want to add the quote to favorites?',
        buttons : [
          {
            text : 'OK',
            handler : () => {
              this.quoteServices.addQuoteToFavorites($list);
              console.log(this.quoteServices);
            }
          },
          {
            text : 'Cancel',
            role : 'cancel',
            handler : ()=> {
              console.log("NO is clicked.");
            }
          }
        ]
      });
  
      alert.present();
    }else{
      const alert = this.alertCtrl.create({
        title : 'Remove Quote',
        message : 'Are Your Sure You want to remove the quote from favorites?',
        buttons : [
          {
            text : 'OK',
            handler : () => {
              this.quoteServices.removeQuoteFromFavorites($list);
              console.log(this.quoteServices);
            }
          },
          {
            text : 'Cancel',
            role : 'cancel',
            handler : ()=> {
              console.log("NO is clicked.");
            }
          }
        ]
      });
  
      alert.present();
    }
    
  }

  checkFav($data){
    // console.log("masuk");
    return this.quoteServices.isFavorite($data);
  }
}



