import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ModalController, ToastController, PopoverController, ViewController } from 'ionic-angular';
import { Quote } from '../../data/quotes.interface';
import { QuotesService } from '../../services/quotes';
import { QuotePage } from '../quote/quote'
import { PopoverPage } from '../popover/popover';
import { SettingService } from '../../services/settings';
import { QuotesPage } from '../quotes/quotes';
/**
 * Generated class for the FavoritesPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-favorites',
  templateUrl: 'favorites.html',
})
export class FavoritesPage {
  quotes : Quote[];
  totalQuotes = 13;

  ngOnInit(){
    this.quotes = this.quoteServices.getAllFavoriteQuotes();
    // console.log("okeoek");
    console.log(this.quotes);
  }

  constructor(private popoverCtrl: PopoverController,public settService: SettingService ,public quoteServices : QuotesService, public navCtrl: NavController, public navParams: NavParams, private modalCtrl: ModalController, private alertCtrl: AlertController, private viewCtrl: ViewController, public toastCtr: ToastController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FavoritesPage');
  }

  addQuote(){
    // this.navCtrl.push(AddQuotePage);
    this.presentPrompt();
  }

  onShowAlert(quote : Quote){
    const alert = this.alertCtrl.create({
      title : 'Remove Quote',
      message : 'Are Your Sure You want to remove the quote from favorites?',
      buttons : [
        {
          text : 'OK',
          handler : () => {
            this.quoteServices.removeQuoteFromFavorites(quote);
            console.log(this.quoteServices);
          }
        },
        {
          text : 'Cancel',
          role : 'cancel',
          handler : ()=> {
            console.log("NO is clicked.");
          }
        }
      ]
    });

    alert.present();
  }

  showModal(temp){
    let modal = this.modalCtrl.create(QuotesPage, {Quote: temp});
    modal.present();
  }

  setBgColor(){
    return this.settService.isAltBackground() ? 'altQuoteBg' : "";
  }

  presentPopover(myEvent){
    let popover = this.popoverCtrl.create(PopoverPage);
    popover.present({
      ev: myEvent
    });
  }

  presentPrompt() {
    let alert = this.alertCtrl.create({
      title: 'Add New Quotes',
      inputs: [
        {
          name: 'author',
          placeholder: 'name'
        },
        {
          name: 'quote',
          placeholder: 'quote'
        }
      ],
      buttons: [
        {
          text: 'OK',
          handler: data => {
            this.quoteServices.addQuoteToFavorites({id:String(this.totalQuotes++), person:data.author, text:data.quote});
            let toast = this.toastCtr.create({
              message : "New Quote is Added!",
              duration : 3000,
              position : "bottom"
            })
            toast.present();
          }
        },
        {
          text: 'CANCEL',
          role: 'cancel',
          handler: data => {
          }
        }
      ]
    });
    alert.present();
  }
}



